/* React CSS */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* General CSS */
.bold {
  font-weight: 200px;
  font-size: 1.5em;
}

body {
  font-family: Georgia, serif;
}
/* Nav CSS */
nav {
  height: 100px;
  line-height: 100px;
}

.navHeader{
  font-size: 3em !important;
}
/* Icons CSS */
i{
  font-size: 3.5em;
}

.icons{
  display: flex;
  justify-content: space-around;
align-items: flex-end;
}
/* Footer CSS */
.footer{
  position:fixed;
  bottom:0;
  left:0;
  width:100%;
}